import React, { useState } from 'react';

export default function Box() {

  const [student, setStudent] = useState({
    username: '',
    email: '',
    address: '',
    course: '',
    gender: '',
    submit: ''
  })
  
  const oninputCange = e => {
    console.log(e.target.name,e.target.value);

    setStudent({
      ...student,
      [e.target.name]: e.target.value
    })
  }

  const onSubmit = e => {
    e.preventDefault();
    console.log();
  }

    return (
      <>
      <div class="jumbotron jumbotron-fluid bg-dark text-light mb-0">
      <div class="container">
        <h1 class="display-4">Please Sign Up to our Website</h1>
        <p class="lead">This is a modified jumbotron that occupies the entire horizontal space of its parent.</p>
        <form 
          className="card bg-secondary text-light px-5" 
          onSubmit={onSubmit}>
        <div className="row pt-3">
        <div className="form-group col-md-4 text-center ">
          <label htmlFor="text">Username</label> 
          <div className="input-group ">
            <div className="input-group-prepend">
              <div className="input-group-text  bg-dark text-light border-0">
                <i className="fa fa-user "></i>
              </div>
            </div>  
            <input 
            id="text" 
            name="Username" 
            placeholder="Enter Username" 
            type="text"
            onBlur={oninputCange}
            defaultValue={student.username} 
            className="form-control bg-dark text-light border-0 text-center"/>
          </div>
        </div>
        <div className="form-group col-md-4 text-center">
          <label htmlFor="email">Email</label> 
          <div className="input-group">
            <div className="input-group-prepend">
              <div className="input-group-text bg-dark text-light border-0">
                <i className="fa fa-envelope"></i>
              </div>
            </div> 
            <input 
            onBlur={oninputCange}
            defaultValue={student.email} 
            id="email" 
            name="email" 
            placeholder="Enter Email" 
            type="text" 
            className="form-control bg-dark text-light border-0 text-center" />
          </div>
        </div>
        <div className="form-group col-md-4 text-center">
          <label htmlFor="address ">Address</label> 
          <div className="input-group">
            <div className="input-group-prepend">
              <div className="input-group-text bg-dark text-light border-0">
                <i className="fa fa-home"></i>
              </div>
            </div> 
            <input 
            onBlur={oninputCange}
            defaultValue={student.address} 
            id="address" 
            name="address" 
            placeholder="Street, Number, City, Zip" 
            type="text" 
            className="form-control bg-dark text-light border-0 text-center" />
          </div>
        </div>
        </div>
​
        <div className="row px-3 mt-0 pt-0">
        <div className="form-group col-md-12 text-center">
          <label htmlFor="select">Course</label> 
          <div>
            <select 
            defaultValue={student.course} 
            name="Course"
            onBlur={oninputCange}  
            className="custom-select bg-dark text-light border-0 text-center">
              <option value="">Select Course</option>
              <option value="physics">Physics</option>
              <option value="Biology">Biology</option>
              <option value="Math">Math</option>
            </select>
          </div>
        </div>
​
        <div className="form-group col-md-12 text-center">
          <label>Gender</label> 
          <div>
            <div className="custom-control custom-radio custom-control-inline">
              <input  
                onFocus={oninputCange} 
                name="gender" 
                id="gender_0" 
                type="radio" 
                className="custom-control-input"
                defaultValue={student.gender}  
                 /> 
                <label 
                htmlFor="gender_0" 
                className="custom-control-label" value="female">Female</label>
            </div>
            <div className="custom-control custom-radio custom-control-inline">
              <input 
              onBlur={oninputCange}
              defaultValue={student.gender}    
              name="gender" 
              id="gender_1" 
              type="radio" 
              className="custom-control-input" 
              /> 
              <label 
              htmlFor="gender_1" 
              className="custom-control-label" value="male" >Male</label>
            </div>
            <div 
            className="custom-control custom-radio custom-control-inline">
              <input 
              onBlur={oninputCange}
              defaultValue={student.gender}    
              name="gender" 
              id="gender_2" 
              type="radio" 
              className="custom-control-input" 
              /> 
              <label 
              htmlFor="gender_2" 
              className="custom-control-label" value="other">Other</label>
            </div>
          </div>
          <button
          name="submit" 
          type="submit"
          className="mt-3 btn btn-outline-dark text-light btn-lg btn-block">Submit</button>
          </div>

          </div>
        </form>
      </div>
      
        </div>
</>
  );

}